<template>
  <el-container class="overview">
    <el-header height="30px">
      <span class="title" style="float: left; font-size: 30px; font-weight: 700"
        >采集表列表</span>
      <div>
        <el-button type="primary" style="float: right" @click="createPaper()"
          >新增采集表</el-button>
      </div>
    </el-header>
    <el-divider></el-divider>

    <el-main class="pappers">
      <h1 v-if="allTables.length === 0"> 无采集表信息，点击<span @click="createPaper()" style="cursor: pointer; color: blue;">新增采集表</span>！</h1>
      <div v-for="(table, index) in allTables" :key="table.id" style="display:block">
        <el-card class="table_card">
          <div slot="header" class="clearfix">
            <span style="float: left; font-size: 18px; font-weight: 700">
              {{ table.title }}
            </span>
            <div style="margin-right: 5px">
              <span style="color: white;">.</span>
              <div style="float: right">
                <span style="margin-right: 10px">ID: {{ table.id }}</span>
                <i class="el-icon-info"/>
                <label v-if="table.status === 'UNPUBLISH'">
                  未发布
                </label>
                <label v-else-if="table.status === 'PUBLISHING'">
                  发布中
                </label>
                <label v-else-if="table.status === 'END'">
                  已结束
                </label>
              </div>
            </div>
          </div>
          <el-button
            v-if="table.status === 'UNPUBLISH'"
            type="text"
            style="float: left"
            @click="editPaper(table.id)"
            icon="el-icon-edit"
            >编辑采集表</el-button
          >
          <el-button
            v-if="table.status === 'PUBLISHING' || table.status === 'END'"
            type="text"
            style="float: left"
            @click="checkPaperDetail(table.id)"
            icon="el-icon-edit">
            查看填写情况
          </el-button>
          <el-button
            v-if="table.status === 'UNPUBLISH'"
            type="text"
            style="float: left"
            @click="releaseTable(table.id)"
            icon="el-icon-share"
            >发布采集表</el-button
          >
          <el-button
            v-if="table.status === 'PUBLISHING'"
            type="text"
            style="float: left"
            @click="showStopDialog(index)"
            icon="el-icon-video-pause"
            >暂停发布</el-button
          >
          <el-button
            type="text"
            style="float: left"
            @click="showDeleteDialog(index)"
            icon="el-icon-delete"
            >删除采集表</el-button
          >
        </el-card>
      </div>
      <el-dialog title="提示" :visible.sync="stopDialogVisible" width="30%">
        <span>确认暂停该采集表的发布</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="stopDialogVisible = false">取 消</el-button>
          <el-button type="danger" @click="handleStop()"
            >确 定</el-button
          >
        </span>
      </el-dialog>
      <el-dialog title="提示" :visible.sync="deleteDialogVisible" width="30%">
        <span>确认删除该采集表</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="deleteDialogVisible = false">取 消</el-button>
          <el-button type="danger" @click="handleDelete()"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
import axios from 'axios'

export default {
  name: 'Overview',
  data() {
    return {
      stopDialogVisible: false,
      deleteDialogVisible: false,
      userId: -1,
      deleteDialogInfo: {
        index: -1
      },
      stopDialogInfo: {
        index: -1
      }
    }
  },
  mounted() {
    // console.log('mounted')
    this.getAdminAllTables()
  },
  computed: {
    ...mapGetters(['allTables'])
  },
  methods: {
    ...mapActions(['getAdminAllTables', 'deleteTable', 'editTable', 'stopReleaseTable']),
    ...mapMutations(['set_createPaperVisible']),
    test() {
      axios({
        url: "/api/token/test",
        method: 'GET',
      }).then(res => {
        console.log(res.data)
        console.log(res.data)
        if (res.data.code == 0) {
          this.$notify.success({title: '测试成功'})
        }
      })
    },
    createPaper() {
      this.set_createPaperVisible(true)
    },
    checkPaperDetail(tableId) {
      console.log(`check paper detail with tableId: ${tableId}`)
      this.$router.push({ name: 'monitor', params: { tableId } })
    },
    releaseTable(tableId) {
      this.$router.push({ name: 'release', params: { tableId } })
    },
    editPaper(tableId) {
      console.log(tableId)
      this.editTable(tableId).then(res => {
        console.log(res)
        if(res) {
          this.$router.push(`/editor/create/${tableId}`)
        } else {
          this.$notify.success({
            title: '异常采集表，请询问管理员'
          })
        }
      })
    },
    handleStop() {
      const index = this.stopDialogInfo.index
      console.info('index: ', index)
      this.stopReleaseTable(index).then(res => {
        console.log(res)
        if(res) {
          this.$notify.success({
            title: '已暂停发布'
          })
        } else {
          this.$notify.error({
            title: '操作失败'
          })
        }
      })
      this.stopDialogVisible = false
    },
    handleDelete() {
      const index = this.deleteDialogInfo.index
      this.deleteTable(index).then(res => {
        console.log(res)
        if(res) {
          this.$notify.success({
            title: '删除成功'
          })
        } else {
          this.$notify.error({
            title: '删除失败'
          })
        }
      })
      this.deleteDialogVisible = false
    },
    showStopDialog(index) {
      this.stopDialogInfo.index = index
      this.stopDialogVisible = true
    },
    showDeleteDialog(index) {
      this.deleteDialogInfo.index = index
      this.deleteDialogVisible = true
    }
  }
}
</script>

<style>
.table_card {
  width: 100%;

}
</style>
